body{
  background-color: #ffeead;
}

.App{
  background-color: #ffcc5c;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 20px; 
  border-color: #fff;
  border: 3px solid white;
}

.header{
  position: relative;
  margin-left: 130px;
  font-size: 50px;
  font-family: 'Patrick Hand SC', cursive;
  color: #fff;
}

#to-do-form input{
  background-color: #80ced6;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
  font-size: 20px;
  font-family: 'Gochi Hand', cursive;
}

#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);
}

#to-do-form input:focus, #to-do-form button:focus{
  outline: none;
}

#to-do-form button{
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 0;
  background-color: #ff6f69;
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
  font-size: 24px;
  font-family: 'Patrick Hand SC', cursive;
}

